import styled from 'styled-components';
import CancelIcon from '@material-ui/icons/Cancel';

const InfoComponent = styled.div`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 20px;
    border-bottom: 1px solid #aaa;
    gap:20px;
`;


const SubDetails = styled.div`
    display: flex;
    flex-direction: column;
    width: 90%;
    padding: 20px;
`;
const SpanHeadDetail = styled.span`
    padding: 8px 8px 10px 8px;
    font-size: 20px;
    font-weight: bold;
`;


const SpanDetail = styled.span`
    padding: 8px;
    font-size: 15px;
    font-weight: bold;
    & span{
        opacity: 0.5;
        font-weight: normal;
    }
`;

function MovieInfoComponent(props){
    return (
        <>
        {props.isLoading ? <div style={{textAlign: 'center'}}>Loading...</div> : 
        <InfoComponent>
            <img src={props.selectedMovie.Poster} alt={props.selectedMovie.Title}/>
            <SubDetails>
                <SpanHeadDetail>Movie: {props.selectedMovie.Title}</SpanHeadDetail>
                <SpanDetail>IMDB Rating: <span>{props.selectedMovie.imdbRating}</span></SpanDetail>
                <SpanDetail>Year: <span>{props.selectedMovie.Year}</span></SpanDetail>
                <SpanDetail>Language: <span>{props.selectedMovie.Language}</span></SpanDetail>
                <SpanDetail>Rated: <span>{props.selectedMovie.Rated}</span></SpanDetail>
                <SpanDetail>Released: <span>{props.selectedMovie.Released}</span></SpanDetail>
                <SpanDetail>Runtime: <span>{props.selectedMovie.Runtime}</span></SpanDetail>
                <SpanDetail>Genre: <span>{props.selectedMovie.Genre}</span></SpanDetail>
                <SpanDetail>Director: <span>{props.selectedMovie.Director}</span></SpanDetail>
                <SpanDetail>Actors: <span>{props.selectedMovie.Actors}</span></SpanDetail>
                <SpanDetail>Plot: <span>{props.selectedMovie.Plot}</span></SpanDetail>
            </SubDetails>
            <CancelIcon style={{cursor: 'pointer'}} onClick={props.onMovieSelect}/>
        </InfoComponent>}
        </>         
    )
}

export default MovieInfoComponent;