import Classes from "./App.module.css";
import MovieComponent from "./component/MovieComponent";
import axios from "axios";
import { useState } from "react";
import MovieInfoComponent from "./component/MovieInfoComponent";

const APIKEY = "eada3e85";

function App() {
  const [timeOutId, setTimeoutId] = useState();
  const [movieData, setMovieData] = useState([]);
  const [selectedMovie, onMovieSelect] = useState();
  const [searchQuery, updateSearchQuery] = useState();
  const [loading, setLoading] = useState(false);

  const fetchData = async (queryString) => {
    if (queryString) {
      try {
        const res = await axios.get(
          `https://www.omdbapi.com/?s=${queryString}&apikey=${APIKEY}`
        );
        if (res.data.Response === "True") {
          setMovieData(res.data.Search);
        } else {
          setMovieData([]);
        }
      } catch (error) {
        console.log(error);
      }
    }
  };

  const onTextChange = (e) => {
    e.preventDefault();
    onMovieSelect("");
    updateSearchQuery(e.target.value);
    clearTimeout(timeOutId);
    const clearTimoutid = setTimeout(async () => {
      fetchData(e.target.value);
    }, 500);
    setTimeoutId(clearTimoutid);
  };

  const displayMovieDetails = async (id) => {
    setLoading(true);
    let movieDetail = await axios.get(
      `https://www.omdbapi.com/?i=${id}&apikey=${APIKEY}`
    );
    onMovieSelect(movieDetail.data);
    setLoading(false);
  };

  return (
    <div className={Classes.container}>
      <div className={Classes.header}>
        <div className={Classes.title}>
          <img
            className={Classes.MovieImg}
            src="./movie-icon.svg"
            alt="Movie_icon"
          />
          <span className={Classes.titleName}>Movie App</span>
        </div>
        <div className={Classes.searchBox}>
          <img src="./search-icon.svg" alt="search_icon" />
          <input
            type="text"
            onChange={onTextChange}
            placeholder="Search Movie"
            value={searchQuery}
          />
        </div>
      </div>
      {selectedMovie && (
        <MovieInfoComponent
          selectedMovie={selectedMovie}
          isLoading = {loading}
          onMovieSelect={onMovieSelect.bind(null, "")}
        />
      )}
      <div className={Classes.MovieListContainer}>
        {!movieData.length ? (
          <div className={Classes.movieIcon}>
            <img src="./movie-icon.svg" alt="Movie_icon" />
          </div>
        ) : (
          movieData.map((movie, id) => (
            <MovieComponent
              key={id}
              movieData={movie}
              onShowMovie={displayMovieDetails}
            />
          ))
        )}
      </div>
    </div>
  );
}

export default App;
